import React, { Fragment } from 'react';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import SearchInput from 'components/input/SearchInput'

interface Props {
  volResults: any,
  input: string,
  error: any,
  handleSubmit: any,
  updateTerm: any
}

const SearchPresenter = ({ input, handleSubmit, updateTerm }: Props) => {
  return (
    <Fragment>
      <SearchInput id="search" type="text" placeholder="   ." value={input} onChange={updateTerm} nametag=" / " handleSubmit={handleSubmit} ></SearchInput>
    </Fragment>
  )
}
export default SearchPresenter;

